import React, { useState, useEffect } from "react";
import { convertToEST } from "./helpers";

function Countdown(props) {
  const [timeLeft, setTimeLeft] = useState(0);

  // eslint-disable-next-line react-hooks/exhaustive-deps
  useEffect(() => {
    const fromTheStartOfDay = convertToEST(
      Math.round((Date.now() / 1000) % 86400)
    );
    const { timeSlot, isWeekend } = props;

    if (isWeekend) {
      setTimeLeft("Imagine Having School on the Weekends");
      return;
    }

    if (timeSlot !== undefined) {
      const timeLeft = timeSlot.end - fromTheStartOfDay;
      let displayString = "";
      if (timeLeft / 3600 >= 1) {
        displayString += `${Math.floor(timeLeft / 3600)}h`;
      }
      if (timeLeft / 60 >= 1) {
        displayString += ` ${Math.floor(Math.floor(timeLeft % 3600) / 60)}m`;
      }
      displayString += ` ${timeLeft % 60}s`;
      setTimeLeft(displayString.trim());
    }
  });
  return <h1>{timeLeft}</h1>;
}

export default Countdown;
