import React, { useState, useEffect } from "react";
import times from "./svg/times.svg";

function dayToString(day) {
  switch (day) {
    case "1":
      return "Monday";
    case "2":
      return "Tuesday";
    case "3":
      return "Wednesday";
    case "4":
      return "Thrusday";
    case "5":
      return "Friday";
    case "any":
      return "Every Day";
    default:
      return "idk → " + day;
  }
}

function SettingsModal(props) {
  const [editors, setEditors] = useState(null);
  const [files, setFiles] = useState(null);

  const handleInput = (id, value) => {
    localStorage.setItem("VRA_" + id, value);
  };

  const handleExport = () => {
    const localStorageExport = {};
    Object.keys(localStorage).forEach((e) => {
      if (e.match(/VRA_[0-9]*/)) {
        localStorageExport[e] = localStorage.getItem(e);
      }
    });
    // elaborate process to download file
    const element = document.createElement("a");
    const file = new Blob([JSON.stringify(localStorageExport)], {
      type: "application/json",
    });
    element.href = URL.createObjectURL(file);
    element.download = "VRA_Export.json";
    document.body.appendChild(element); // Required for this to work in FireFox
    element.click();
  };

  const handleImport = () => {
    var reader = new FileReader();
    reader.onload = (function (reader) {
      return () => {
        var contents = reader.result;
        //var lines = contents.split("\n");
        ////// data ready
        const impored = JSON.parse(contents);
        Object.keys(impored).forEach((element) => {
          localStorage.setItem(element, impored[element]);
        });
        window.location.reload();
      };
    })(reader);

    reader.readAsText(files);
  };

  useEffect(() => {
    const { times } = props;
    if (!!times || !typeof times === "undefined") {
      // parsing
      const out = {};

      Object.keys(times).forEach((key) => {
        out[key] = [];
        times[key].forEach((key2) => {
          if (key2.richContext !== undefined) {
            const lsc = localStorage.getItem("VRA_" + key2.richContext);
            let preFillValue = "";
            if (lsc !== null) preFillValue = lsc;
            out[key].push(
              <React.Fragment key={key2.richContext}>
                <p>
                  {key2.event} | ID: {key2.richContext}
                </p>
                <div className="richContextContainer">
                  <p className="editorLabel">Label: </p>
                  <input
                    className="richContextEditor"
                    type="text"
                    defaultValue={preFillValue}
                    onInput={(e) =>
                      handleInput(key2.richContext, e.target.value)
                    }
                  />
                </div>
              </React.Fragment>
            );
          }
        });
      });

      // displaying
      let outFormated = [];
      Object.keys(out).forEach((key) => {
        const dti = dayToString(key.toString());
        outFormated.push(<h2 key={dti}>{dti}</h2>);
        //outFormated.concat(out[key]);
        outFormated = [...outFormated, ...out[key]];
      });
      setEditors(outFormated);
    }

    // bind to escape key (same as pressing the x)
    const handleKeyPress = (e) => {
      e.preventDefault();
      if (e.key === "Escape") {
        props.handleCloseSettings();
      }
    };
    document.addEventListener("keydown", handleKeyPress);

    return () => {
      document.removeEventListener("keydown", handleKeyPress);
    };

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div className="modal">
      <img
        src={times}
        alt="Close!"
        className="modal-close"
        onClick={() => props.handleCloseSettings()}
      />
      <div className="richContextEditors">
        {editors}{" "}
        <div className="footer">
          <button onClick={() => handleExport()}>Export!</button>
          <input
            type="file"
            accept=".json, .txt"
            onChange={(event) => {
              setFiles(event.target.files[0]);
            }}
            onClick={(event) => {
              event.target.value = null;
            }}
          ></input>
          <button onClick={() => handleImport()}>Submit</button>
        </div>
      </div>
    </div>
  );
}

export default SettingsModal;
